<h1 id="python-conditions" tabindex="-1"><a class="header-anchor" href="#python-conditions" aria-hidden="true">#</a> Python Conditions</h1>
<p>In Python we can use the <code>if</code> statement to conditionally execute code. For example, if we ask the user to enter his age, we can make different things happen depending on which age the user, such as printing <code>You're young.</code> or <code>You're old.</code> depending on if the age is lower than 30 or not. In this lecture we take a closer look at how to achieve this.</p>
